@use '../util' as *;

.header {
  background: url(../../../img/mobile/image-header.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;

  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: rem(25);
    margin-bottom: rem(70);

    .logo {
      width: 98px;
      height: 19px;
    }

    .menu-icon {
      width: 20px;
      background: none;
      border: none;
      cursor: pointer;
    }

    .menu {
      display: none;
    }
  }

  .hero {
    text-align: center;
    display: grid;
    place-items: center;

    h1 {
      font-family: var(--ff-fraunces);
      font-weight: 900;
      text-transform: uppercase;
      color: var(--white);
      font-size: rem(38.4);
      line-height: initial;
      letter-spacing: rem(8);
      margin-bottom: rem(44);
    }

    .arrow {
      width: 36px;
      height: 114px;
      margin-bottom: rem(104);
    }
  }
}